import mixins from "../../../utils/mixins";
import LayoutTb from "../../../components/Layout/LayoutTb";
import MyInputButton from "../../../components/search/MyInputButton";
import MyPagination from "../../../components/MyPagination";
import DialogNormal from "../../../components/utils/dialog/DialogNormal";
import MyNormalDialog from "../../../components/utils/dialog/MyNormalDialog";
import DrawerRight from "../../../components/utils/dialog/DrawerRight";
import MyEnable from "../../../components/utils/status/MyEnable";
import {common} from "../../../utils/api/url";
import {
  setChildren,
  MessageSuccess,
  MessageError,
  courseProperties,
  teachMethod,
  examMethod
} from "../../../utils/utils";
import MySelect from "../../../components/MySelect";
import MyRadio from "../../../components/MyRadio";
import courseManageValidater from "../../../utils/validater/courseManageValidater";

export default {
  mixins: [mixins, courseManageValidater],
  components: {MyRadio, MySelect, LayoutTb,MyInputButton,MyPagination,DialogNormal,MyNormalDialog,DrawerRight,MyEnable},
  data(){
    return {
      modalVisible: false,
      drawerVisible: false,
      visibleConfim: false,
      dialogLoading: false,
      loading: false,
      drawerLoading: false,
      subDetail: '',
      uploadFile: common.course_mamage_file,
      uploadAction: common.course_mamage_import,
      uploadResult: {},
      uploadProcess: '',
      g_superId: '',
      tableData: [],
      searchKey: '',
      form: {
        id: '',
        courseName: '',
        courseNo: '',
        name: '',
        teachType: '',
        courseProperty: '',
        examType: '',
        credit: '',
        enabled: true
      },
    }
  },
  created() {
    this.init();
  },
  methods: {
    init(){
      let params = {
        page: this.page,
        num: this.num,
        courseName: this.searchKey
      };
      this.$axios.get(common.course_mamage_page, {params: params}).then(res => {
        if (res.data.data){
          this.tableData = res.data.data.list;
          this.total = res.data.data.totalCount;
          this.num = res.data.data.num;
          this.page = res.data.data.currentPage;
        }
      });
    },
    addInfo(){
      this.modalVisible = true;
    },
    editInfo(row){
      this.form = {
        id: row.id,
        courseName: row.course_name,
        courseNo: row.course_no,
        name: row.name,
        teachType: ''+row.teach_type,
        courseProperty: ''+row.course_property,
        examType: ''+row.exam_type,
        credit: row.credit,
        enabled: row.enabled
      };
      this.modalVisible = true;
    },
    deleteInfo(row){
      this.form.id = row.id;
      this.subDetail = row.course_name;
      this.visibleConfim = true;
    },
    sizeChange(event){
      this.page = 1;
      this.num = event;
      this.init();
    },
    currentPage(event){
      this.page = event;
      this.init();
    },
    jumpPage(data){
      this.page = data;
      this.init();
    },
    uploadOrganization(event){
      this.drawerVisible = true;
    },
    cancelDialog(){
      this.modalVisible = false;
    },
    cancelDrawDialog(){
      this.uploadProcess = '';
      this.uploadResult = {};
      this.drawerVisible = false;
    },
    closeDialog(event){
      this.form = {
        id: '',
        courseName: '',
        courseNo: '',
        name: '',
        teachType: '',
        courseProperty: '',
        examType: '',
        credit: '',
        enabled: true
      };
      if (this.$refs['form']){
        this.$refs['form'].resetFields();
      }
    },
    closeDrawerDialog(event){
      this.uploadProcess = '';
      this.uploadResult = {};
      this.drawerVisible = event;
    },
    okDialog(event){
      let url = "";
      let params = {
        courseNo: this.form.courseNo,
        courseName: this.form.courseName,
        teachType: this.form.teachType,
        courseProperty: this.form.courseProperty,
        examType: this.form.examType,
        credit: this.form.credit,
        enabled: this.form.enabled
      };
      if (this.form.id && this.form.id != ""){
        url = common.course_mamage_edit;
        params['id'] = this.form.id;
      }else {
        url = common.course_mamage_add;
      }
      params = this.$qs.stringify(params);

      this.$refs['form'].validate((valid) => {
        if (valid) {
          this.dialogLoading = true;
          this.$axios.post(url, params, {loading: false}).then(res=>{
            if (res.data.code == 200){
              this.init();
              MessageSuccess(res.data.desc);
            }else {
              MessageError(res.data.desc);
            }
            this.dialogLoading = false;
            this.modalVisible = false;
          });
        }
      });
    },
    closeDrawDialog(event){
      this.drawerVisible = false;
    },
    okDrawDialog(event){
      this.drawerLoading = true;
      setTimeout(() => {
        this.$message({
          message: '恭喜你，这是一条成功消息',
          type: 'success'
        });
        this.drawerLoading = false;
        this.drawerVisible = false;
      },2000)
    },
    handleOkChange(data) {
      this.dialogLoading = true;
      let params = {
        courseId: this.form.id
      };
      //params = this.$qs.stringify(params);
      this.$axios.get(common.course_mamage_del, {params:params}).then(res => {
        if (res.data.code == 200){
          this.init();
          MessageSuccess(res.data.desc);
        }else {
          MessageError(res.data.desc);
        }
        this.visibleConfim = false;
        this.dialogLoading = false;
      });
    },
    handleCancelChange(data) {
      this.visibleConfim = false;
    },
    search(data){
      this.page = 1;
      this.searchKey = data.input;
      this.init();
    },
    changeStatus(event, data){
      this.form.enabled = data;
    },
    handleChange(data, type){
      switch (type) {
        case 1:
          this.form.teachType = ''+data;
          break;
        case 2:
          this.form.courseProperty = ''+data;
          break;
        case 3:
          this.form.examType = ''+data;
          break;
      }
    },
    coursePropertiesInfo(type, value){
      if (type == 'get'){
        let arr = courseProperties(type);
        let arrObj = [];
        for (let item in arr){
          arrObj.push({
            value: item,
            label: arr[item]
          });
        }
        return arrObj;
      }else {
        return courseProperties(type, value);
      }
    },
    teachMethodInfo(type, value){
      if (type == 'get'){
        let arr = teachMethod(type);
        let arrObj = [];
        for (let item in arr){
          arrObj.push({
            value: item,
            label: arr[item]
          });
        }
        return arrObj;
      }else {
        return teachMethod(type, value);
      }
    },
    examMethodInfo(type, value){
      if (type == 'get'){
        let arr = examMethod(type);
        let arrObj = [];
        for (let item in arr){
          arrObj.push({
            value: item,
            label: arr[item]
          });
        }
        return arrObj;
      }else {
        return examMethod(type, value);
      }
    },
    uploadSuccess(res, file){
      this.uploadProcess = res.desc;


      if (res.code == 200){
        this.uploadResult = res.data ? res.data : [res.desc];
      }else {
        if (res.data){
          this.uploadResult = res.data;
        }else {
          this.uploadResult = [res.desc];
        }
      }
    },
    uploadError(res, file){
      this.uploadProcess = res.data.data;
    }
  }
}
